import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonModule, MatDialogModule, MatSelectModule, MatIconModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { AgGridModule } from 'ag-grid-angular';

import { GradebookRoutingModule } from './gradebook-routing.module';
import {
  GradebookComponent
} from './gradebook.component';
import { GradebookSummaryComponent } from './gradebook-summary/gradebook-summary.component';
import { GradebookAssignmentsComponent } from './gradebook-assignments/gradebook-assignments.component';
import { GradebookCategoriesComponent } from './gradebook-categories/gradebook-categories.component';

@NgModule({
  imports: [
    CommonModule,
    GradebookRoutingModule,
    MatButtonModule,
    MatDialogModule,
    MatSelectModule,
    MatIconModule,
    FlexLayoutModule,
    AgGridModule.withComponents([])
  ],
  declarations: [
    GradebookComponent,
    GradebookSummaryComponent,
    GradebookAssignmentsComponent,
    GradebookCategoriesComponent
  ],
  entryComponents: [GradebookAssignmentsComponent]
})
export class GradebookModule {}
