import { Component, OnInit, Inject} from '@angular/core';
import {MatDialog} from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { switchMap, map } from 'rxjs/operators';

import { GradebookAssignmentsComponent } from './gradebook-assignments/gradebook-assignments.component';

@Component({
  selector: 'app-gradebook',
  templateUrl: './gradebook.component.html',
  styleUrls: ['./gradebook.component.css']
})
export class GradebookComponent implements OnInit {
  animal: string;
  name: string;
  selectedTerm = 'option3';
  selectedCategory = '-2';

  constructor(public dialog: MatDialog, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    if(this.route.firstChild) {
      this.selectedCategory = this.route.firstChild.snapshot.params.id;
    }
  }

  onCategoryChange(selectedCategoryId): void {
    if (selectedCategoryId == -2) {
      this.router.navigate(['/gradebook/summary']);
    } else {
      this.router.navigate(['/gradebook/categories/' + selectedCategoryId ]);
    }
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(GradebookAssignmentsComponent, {
      width: '250px',
      data: {name: this.name, animal: this.animal}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.animal = result;
    });
  }

}
