import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LunchVerificationComponent } from './lunch-verification.component';

const routes: Routes = [
  {
    path: '',
    component: LunchVerificationComponent,
    data: { postMessage: true }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LunchVerificationRoutingModule { }
