import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { GradebookComponent } from './gradebook.component';
import { GradebookSummaryComponent } from './gradebook-summary/gradebook-summary.component';
import { GradebookAssignmentsComponent } from './gradebook-assignments/gradebook-assignments.component';
import { GradebookCategoriesComponent } from './gradebook-categories/gradebook-categories.component';

const routes: Routes = [
  {
    path: '',
    component: GradebookComponent,
    data: { postMessage: true },
    children: [
      {
        path: 'summary',
        component: GradebookSummaryComponent
      },
      {
        path: 'categories/:id',
        component: GradebookCategoriesComponent
      },
      {
        path: '',
        redirectTo: 'summary',
        pathMatch: 'full'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GradebookRoutingModule {}
