import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-gradebook-assignments',
  templateUrl: './gradebook-assignments.component.html',
  styleUrls: ['./gradebook-assignments.component.css']
})
export class GradebookAssignmentsComponent {

  constructor(
    public dialogRef: MatDialogRef<GradebookAssignmentsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}
